import React, {
  useState,
  useEffect,
  useRef,
  useContext,
  useCallback,
} from "react";

import AuthContext from "context/AuthProvider";

import "./Slider.scss";

const CitiesSlider = ({ slides }) => {
  const IMAGE_PARTS = 4;
  const AUTOCHANGE_TIME = 4000;
  const [activeSlide, setActiveSlide] = useState(-1);
  const [prevSlide, setPrevSlide] = useState(-1);
  const [sliderReady, setSliderReady] = useState(false);
  const changeTO = useRef(null);

  useEffect(() => {
    const runAutochangeTO = () => {
      changeTO.current = setTimeout(() => {
        changeSlides(1);
        runAutochangeTO();
      }, AUTOCHANGE_TIME);
    };

    runAutochangeTO();

    setTimeout(() => {
      setActiveSlide(0);
      setSliderReady(true);
    }, 0);

    return () => clearTimeout(changeTO.current);
  }, []);

  const changeSlides = (change) => {
    clearTimeout(changeTO.current);
    const length = slides.length;
    const newPrevSlide = activeSlide;
    let newActiveSlide = newPrevSlide + change;
    if (newActiveSlide < 0) newActiveSlide = length - 1;
    if (newActiveSlide >= length) newActiveSlide = 0;
    setActiveSlide(newActiveSlide);
    setPrevSlide(newPrevSlide);
  };

  return (
    <div className={`slider ${sliderReady ? "s--ready" : ""}`}>
      <p className="slider__top-heading">Промокод</p>
      <div className="slider__slides">
        {slides.map((slide, index) => (
          <div
            className={`slider__slide ${
              activeSlide === index ? "s--active" : ""
            } ${prevSlide === index ? "s--prev" : ""}`}
            key={index}
          >
            <div className="slider__slide-content">
              <h3 className="slider__slide-subheading">
                {slide.country || slide.city}
              </h3>
              <h2 className="slider__slide-heading">
                {slide.city.split("").map((l, i) => (
                  <span key={i}>{l}</span>
                ))}
              </h2>
            </div>
            <div className="slider__slide-parts">
              {[...Array(IMAGE_PARTS).keys()].map((i) => (
                <div className="slider__slide-part" key={i}>
                  <div
                    className="slider__slide-part-inner"
                    style={{ backgroundImage: `url('${slide.img}')` }}
                  />
                </div>
              ))}
            </div>
          </div>
        ))}
      </div>
      <div className="slider__control" onClick={() => changeSlides(-1)} />
      <div
        className="slider__control slider__control--right"
        onClick={() => changeSlides(1)}
      />
    </div>
  );
};

const Slider = () => {
  const { auth } = useContext(AuthContext);
  const slides = [
    {
      city: auth.accessToken?.code || auth?.promocode,
      country: "Ваш промокод",
      img: "../../UI/IMG_9794.jpg",
    },
  ];

  return <CitiesSlider slides={slides} />;
};

export { Slider };
